body {
  padding: 0;
  margin: 0;
  font-family: "Inter", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0.6px;
  background-color: #f2f2f2;
}

* {
  box-sizing: border-box;
}

.--font-bold {
  font-weight: 700;
}

.loc {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;

  &__layout {
    position: relative;
    overflow: visible;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    // max-height: 100vh;
    min-height: 100vh;
    padding-bottom: 62px;

    // &:before {
    //   content: "";
    //   z-index: -1;
    //   position: fixed;
    //   bottom: 0;
    //   width: 100%;
    //   height: 100vh;
    //   background-image: url("../../assets/images/bg.png");
    //   background-position: center 70vh;
    //   background-size: cover;
    //   background-repeat: no-repeat;
    //   opacity: 0.7;
    // }
  }

  &__header {
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: 180px;
    padding: 20px;
    background-color: #ffebcd;
    transition: all 0.3s ease-in-out;

    &--mini {
      height: 60px;
      padding: 0 20px;
      border-bottom: 1px solid #dddddd;
    }
  }

  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-size: 20px;
    text-transform: uppercase;
    text-align: center;
    margin: 0;
    padding: 0;
    font-weight: 500;
    color: #333;
    // height: 150px;

    &-h2 {
      height: auto;
      margin-bottom: 12px;
    }
  }

  &__logo {
    width: 70px;
    transition: all 0.3s ease-in-out;
    position: absolute;
    left: calc(50% - 35px);
    top: 20px;

    &--mini {
      top: 8px;
      left: 20px;
      width: 30px;
    }

    &-name {
      width: 21vw;
      transition: all 0.3s ease-in-out;

      &--mini {
        margin-top: 0;
      }
    }

    &-slogan {
      width: 40vw;
      transition: all 0.3s ease-in-out;

      &--hidden {
        opacity: 0;
      }
    }
  }

  &__data-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 100%;
    padding: 16px 12px;
  }

  &__label {
    color: #777;
    margin-bottom: 6px;
    font-size: 14px;
  }

  &__data {
    margin-bottom: 12px;
  }

  &__card {
    background-color: #fff;
    padding: 12px;
  }
}
